'use client';

import { getTestimonials } from '@/app/services/testimonialsService';
import { useEffect, useState } from 'react';
import VideoTestimonials from './TestimonialsList';

const TestimonialsSkeleton = () => {
  return (
    <section className="py-16 md:py-24 bg-white dark:bg-foreground">
      <div className="container mx-auto text-center animate-pulse">
        {/* Label */}
        <div className="h-4 w-32 bg-gray-300 dark:bg-gray-700 mx-auto mb-4 rounded" />

        {/* Title */}
        <div className="h-10 w-2/3 bg-gray-300 dark:bg-gray-700 mx-auto rounded" />

        {/* Cards */}
        <div className="mt-10 grid grid-cols-1 md:grid-cols-1 gap-6">
          {Array.from({ length: 1 }).map((_, i) => (
            <div key={i} className="rounded-xl bg-gray-200 dark:bg-gray-700 h-72" />
          ))}
        </div>
      </div>
    </section>
  );
};

export interface TestimonialCard {
  id: number;
  text: string;
  name: string;
  designation: string;
}

export interface TestimonialsSection {
  id: number;
  documentId: string;
  label: string;
  description: string;
  createdAt: string;
  updatedAt: string;
  publishedAt: string;
  card: TestimonialCard[];
}

export default function Testimonials() {
  const [testimonials, setTestimonials] = useState<TestimonialsSection | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchTestimonials = async () => {
      try {
        const response = await getTestimonials();
        setTestimonials(response?.data ?? null);
      } finally {
        setLoading(false);
      }
    };

    fetchTestimonials();
  }, []);

  if (loading) {
    return <TestimonialsSkeleton />;
  }

  if (!testimonials) {
    return null;
  }

  return (
    <section id="projects" className="py-16 md:py-24 dark:bg-foreground bg-white">
      <div className="container mx-auto text-center">
        <p className="text-sm font-medium text-gray-400 mb-2 uppercase tracking-wider">{testimonials.label}</p>

        <h1 className="text-3xl md:text-5xl font-extrabold leading-tight">{testimonials.description}</h1>

        <div className="mt-10">
          <VideoTestimonials data={testimonials.card} />
        </div>
      </div>
    </section>
  );
}
