'use client';

import SeoHead from '@/app/components/seo/seoHead';
import { getPaginatedBlogs } from '@/app/services';
import Image from 'next/image';
import Link from 'next/link';
import { useEffect, useState } from 'react';
import BlogCard from './blogCard';

/* -------------------------------- TYPES -------------------------------- */
type Blog = {
  coverImage?: { url?: string };
  title: string;
  slug: string;
  author?: string;
  publishedAt: string;
  excerpt?: string;
  video?: { url?: string }[];
  description?: string;
  is_main?: boolean;
};

/* ----------------------------- IN-MEMORY CACHE ----------------------------- */
const blogCache: {
  data: Blog[] | null;
  fetchedAt: number | null;
} = {
  data: null,
  fetchedAt: null,
};

/* -------------------------------- UTILS -------------------------------- */
const formatDate = (date: string) =>
  new Date(date).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
  });

/* ------------------------------ SKELETON UI ------------------------------ */
function BlogSkeleton({ variant = 'small' }: { variant?: 'small' | 'featured' }) {
  return (
    <div className="border rounded-lg p-3 dark:bg-secondary-dark animate-pulse">
      <div className={`w-full ${variant === 'featured' ? 'h-60' : 'h-36'} bg-gray-300 dark:bg-gray-700 rounded mb-4`} />
      <div className="h-5 bg-gray-300 dark:bg-gray-700 rounded w-3/4 mb-3" />
      <div className="h-3 bg-gray-300 dark:bg-gray-700 rounded w-1/2 mb-2" />
      <div className="h-3 bg-gray-300 dark:bg-gray-700 rounded w-1/3" />
    </div>
  );
}

/* ------------------------------ COMPONENT ------------------------------ */
export default function BlogsHomePage() {
  const [blogs, setBlogs] = useState<Blog[]>([]);
  const [loading, setLoading] = useState(true);

  /* ------------------------------ DATA FETCH ------------------------------ */
  useEffect(() => {
    if (blogCache.data) {
      setBlogs(blogCache.data);
      setLoading(false);
      return;
    }

    const fetchBlogs = async () => {
      try {
        setLoading(true);
        const res = await getPaginatedBlogs(1, 6);
        const data = res?.data ?? [];

        setBlogs(data);
        blogCache.data = data;
        blogCache.fetchedAt = Date.now();
      } catch (error) {
        console.error('Failed to fetch blogs:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchBlogs();
  }, []);

  /* ------------------------------ DERIVED DATA ------------------------------ */
  const featuredBlog = blogs.find((b) => b.is_main === true);
  const leftBlogs = blogs.filter((b) => !b.is_main).slice(0, 2);
  const rightBlogs = blogs.filter((b) => !b.is_main).slice(2, 5);

  const seoUrl = `${process.env.NEXT_PUBLIC_BASE_URL || 'https://insonix.com'}/`;

  /* ------------------------------ RENDER ------------------------------ */
  return (
    <>
      {/* SEO */}
      <SeoHead
        title="Blog Stories | Insonix"
        description="Read the latest blog stories, insights, and updates from Dolcera team."
        keywords="blogs, tech blogs, insonix blogs, development, software"
        image="/default-og.jpg"
        url={seoUrl}
        author="Insonix Team"
      />

      <section className="w-full py-16 md:py-24 bg-white dark:bg-foreground ">
        <div className="container mx-auto text-center">
          <p className="text-sm font-medium uppercase tracking-wider mb-2 text-gray-800 dark:text-gray-400">
            Dolcera Insights
          </p>

          <h2 className="text-3xl md:text-5xl font-extrabold mb-10 dark:text-white">Thoughts on the Digital Horizon</h2>

          <main className="container mx-auto max-w-full px-4 py-10">
            <div className="grid md:grid-cols-8 gap-6">
              {/* ---------------- LEFT BLOGS ---------------- */}
              <aside className="md:col-span-2 space-y-4">
                {leftBlogs.map((blog) => (
                  <BlogCard key={blog.slug} blog={blog} variant="left" />
                ))}
              </aside>

              {/* ---------------- FEATURED BLOG ---------------- */}
              <main className="md:col-span-4">
                {featuredBlog && <BlogCard blog={featuredBlog} variant="featured" />}
              </main>

              {/* ---------------- RIGHT BLOGS ---------------- */}
              <aside className="md:col-span-2 space-y-4">
                {loading
                  ? Array.from({ length: 3 }).map((_, i) => <BlogSkeleton key={i} />)
                  : rightBlogs.map((blog) => (
                      <article
                        key={blog.slug}
                        className="group relative text-left h-[160px] overflow-hidden rounded-sm bg-white shadow-xl"
                      >
                        <div className="absolute inset-0 flex flex-col justify-end bg-gradient-to-t from-white via-white to-transparent  px-5 py-4  md:translate-y-[20%] translate-y-[0%] transition-transform duration-[500ms] ease-out group-hover:translate-y-0">
                          <p className="mb-2 text-sm dark:text-gray-300 opacity-80">{formatDate(blog.publishedAt)}</p>

                          <h2 className="text-xl">{blog.title}</h2>

                          <p className="mt-4 text-sm ">{blog.description}</p>

                          <Link
                            href={`/blogs/detail/${blog.slug}`}
                            className="mt-4 block font-semibold text-primary-200 opacity-0 transition-opacity duration-[500ms] group-hover:opacity-100"
                          >
                            Read More â†’
                          </Link>
                        </div>
                      </article>
                    ))}
              </aside>
            </div>

            <Link href="/blogs" className="mt-10 inline-block text-blue-500 hover:underline">
              View All Blogs â†’
            </Link>
          </main>
        </div>
      </section>
    </>
  );
}
