'use client';

import { getProjects } from '@/app/services/projectService';
import { ProjectCard } from '@/app/utils/Interfaces';
import Link from 'next/link';
import { useEffect, useState } from 'react';
let cachedProjects: ProjectCard[] | null = null;

function ProjectSkeleton() {
  return (
    <div className="w-full h-87.5 rounded-lg bg-gray-200 dark:bg-gray-800 animate-pulse relative overflow-hidden">
      <div className="absolute inset-0 flex flex-col justify-between p-6">
        <div />

        <div className="space-y-3">
          <div className="h-4 w-24 bg-gray-300 dark:bg-gray-700 rounded" />
          <div className="h-6 w-3/4 bg-gray-300 dark:bg-gray-700 rounded" />
        </div>

        <div className="h-8 w-40 bg-gray-300 dark:bg-gray-700 rounded-full mx-auto" />
      </div>
    </div>
  );
}

export default function Projects() {
  const [projects, setProjects] = useState<ProjectCard[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    const fetchProjects = async () => {
      try {
        setLoading(true);

        if (cachedProjects) {
          setProjects(cachedProjects);
          return;
        }

        const response = await getProjects();
        const data: ProjectCard[] = response?.data || [];

        setProjects(data);
        cachedProjects = data;
      } catch (err) {
        console.error('Failed to fetch projects', err);
        setError(true);
      } finally {
        setLoading(false);
      }
    };

    fetchProjects();
  }, []);

  return (
    !loading &&
    projects?.length > 0 && (
      <section id="projects" className="py-16 md:px-0 px-4 md:py-24 dark:bg-foreground bg-white">
        <div className="container mx-auto text-center">
          <p className="text-center text-gray-400 uppercase tracking-wider">What We Do</p>
          <h1 className="text-center text-3xl md:text-5xl font-extrabold mb-10">Our Projects</h1>

          {/* ðŸ”¹ GRID */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {/* ðŸ”¹ Skeleton Loader */}
            {loading && Array.from({ length: 6 }).map((_, i) => <ProjectSkeleton key={i} />)}

            {/* ðŸ”¹ Error */}
            {!loading && error && <p className="col-span-full text-center text-red-500">Failed to load projects</p>}

            {/* ðŸ”¹ Projects */}
            {!loading &&
              !error &&
              projects.slice(0, 5).map((project: any, index) => {
                const isLast = index === projects.length - 1;
                return (
                  <Link
                    key={index}
                    href={project?.link ?? '#'}
                    className={`group w-full h-87.5 relative overflow-hidden shadow-lg cursor-pointer block
        ${isLast ? 'md:col-span-2' : ''}
      `}
                  >
                    {/* Background */}
                    <div
                      className="absolute inset-0 bg-cover bg-center transition-transform duration-500 group-hover:scale-110"
                      style={{ backgroundImage: `url(${project?.image?.url})` }}
                    />

                    {/* Overlay */}
                    <div className="absolute inset-0 bg-primary-900/80 group-hover:bg-black/20 transition-all duration-500" />

                    {/* Content */}
                    <div className="relative h-full flex flex-col items-center justify-between text-white p-6">
                      <div />

                      <div className="text-center">
                        <p className="text-lg mb-3">{project?.project_category?.label}</p>
                        <h1 className="font-bold">{project?.label}</h1>
                      </div>

                      <div className="w-full flex justify-center">
                        <span className="inline-block rounded-full bg-secondary px-5 py-2 text-sm font-semibold uppercase opacity-0 group-hover:opacity-100 translate-y-2 group-hover:translate-y-0 transition-all duration-300">
                          Explore Projects â†’
                        </span>
                      </div>
                    </div>
                  </Link>
                );
              })}
          </div>

          {/* ðŸ”¹ View All */}
          {!loading && projects?.length > 5 && (
            <Link
              href="/projects"
              className="mt-10 mx-auto text-center w-full inline-block text-blue-500 hover:underline"
            >
              View All Projects â†’
            </Link>
          )}
        </div>
      </section>
    )
  );
}
