'use client';

import { Avatar } from 'flowbite-react';
import { useEffect, useRef, useState } from 'react';

interface CardItem {
  id: number;
  label: string;
  image?: {
    url?: string;
    formats?: {
      thumbnail?: { url: string };
      small?: { url: string };
    };
  };
}

export default function CardSlider({ data = [] }: { data: CardItem[] }) {
  const sliderRef = useRef<HTMLDivElement>(null);
  const [active, setActive] = useState(0);
  const [paused, setPaused] = useState(false);

  const CARD_WIDTH = 320;
  const TOTAL = data.length;

  /* ---------------- Auto Scroll ---------------- */
  useEffect(() => {
    if (paused || TOTAL === 0) return;

    const id = setInterval(() => {
      scrollTo((active + 1) % TOTAL);
    }, 3000);

    return () => clearInterval(id);
  }, [active, paused, TOTAL]);

  /* ---------------- Helpers ---------------- */
  const scrollTo = (index: number) => {
    if (!sliderRef.current) return;

    sliderRef.current.scrollTo({
      left: index * CARD_WIDTH,
      behavior: 'smooth',
    });

    setActive(index);
  };

  const handleScroll = () => {
    if (!sliderRef.current) return;

    const index = Math.round(sliderRef.current.scrollLeft / CARD_WIDTH);
    setActive(Math.min(Math.max(index, 0), TOTAL - 1));
  };

  const handleKey = (e: React.KeyboardEvent) => {
    if (e.key === 'ArrowRight') scrollTo((active + 1) % TOTAL);
    if (e.key === 'ArrowLeft') scrollTo((active - 1 + TOTAL) % TOTAL);
  };

  const getImageUrl = (card: CardItem) =>
    card.image?.formats?.thumbnail?.url || card.image?.formats?.small?.url || card.image?.url || '';

  if (!TOTAL) return null;

  return (
    <section
      className="relative mx-auto mt-10 outline-none"
      tabIndex={0}
      onKeyDown={handleKey}
      aria-roledescription="carousel"
      aria-label="Client slider"
    >
      {/* SLIDER */}
      <div
        ref={sliderRef}
        onScroll={handleScroll}
        onMouseEnter={() => setPaused(true)}
        onMouseLeave={() => setPaused(false)}
        className="
          flex overflow-x-hidden snap-x snap-mandatory gap-4 pb-6
          scrollbar-hide scroll-smooth
        "
      >
        {data.map((card, i) => (
          <div
            key={card.id}
            role="group"
            aria-label={`Slide ${i + 1}`}
            className="
              snap-start shrink-0 w-[320px]
              p-4 rounded-lg bg-white shadow-lg
              transition hover:scale-[1.02]
              flex flex-col items-center cursor-pointer
            "
          >
            <Avatar
              img={getImageUrl(card)}
              size="xl"
              rounded={false}
              className="grayscale hover:grayscale-0 transition"
            />

            <p className="mt-5 text-primary-900 font-semibold text-center">{card.label}</p>
          </div>
        ))}
      </div>

      {/* DOTS */}
      <div className="mt-4 flex justify-center gap-2">
        {data.map((_, index) => (
          <button
            key={index}
            aria-label={`Go to slide ${index + 1}`}
            onClick={() => scrollTo(index)}
            className={`h-3 w-3 rounded-full transition ${
              active === index ? 'bg-gray-800 scale-110' : 'bg-gray-300 hover:bg-gray-500'
            }`}
          />
        ))}
      </div>
    </section>
  );
}
